:root {
    --color-layer-1: #f6f6f6;
    --color-layer-2: #ebebeb;
    --color-layer-3: #bebebe;
}

.app {
    min-height: 90vh;
}

.theme-d1 {
    background-color: var(--color-layer-2, #ebebeb);
}

.theme-l1 {
    background-color: var(--color-layer-1, #f6f6f6);
}

.theme-d2 {
    background-color: var(--color-layer-3, #bebebe);
}