input[type=range].productivity {
    -webkit-appearance: none;
    appearance: none;
    background-color: var(--color-layer-3, #bebebe);
    border-radius: 1em;
    height: 0.5em;
    margin-top: 0.5em;
}

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    border: 1px solid var(--color-layer-3, #bebebe);
    background-color: var(--color-layer-1, #f6f6f6);
    cursor: pointer;
}

input[type=range]::-moz-range-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    border: 1px solid var(--color-layer-3, #bebebe);
    background-color: var(--color-layer-1, #f6f6f6);
    cursor: pointer;
}

span.productivity {
    text-align: center;
}